import { Duration } from './duration';
export class Time {
    constructor(millis) {
        this.millis = millis;
    }
    static now() {
        return new Time(Date.now());
    }
    plus(duration) {
        return new Time(this.millis + duration['millis']); // we would need a C++-style 'friend' class
    }
    differenceTo(time) {
        return new Duration(Math.abs(time.millis - this.millis));
    }
    date() {
        return new Date(this.millis);
    }
    format(callback) {
        return callback(new Date(this.millis));
    }
}
Time.localTimeFormatter = (date) => {
    return new Intl.DateTimeFormat('en-US', {
        hour: '2-digit',
        minute: '2-digit',
        second: '2-digit',
        timeZone: Intl.DateTimeFormat().resolvedOptions().timeZone,
        hour12: false
    }).format(date);
};
