export class Duration {
    constructor(millis) {
        if (millis < 0) {
            throw new Error('the parameter "millis" must be >= 0');
        }
        this.millis = millis;
    }
    static ofSeconds(seconds) {
        return new Duration(seconds * this.ONE_SECOND);
    }
    isLessThan(other) {
        return this.millis < other.millis;
    }
    isGreaterThan(other) {
        return this.millis > other.millis;
    }
    times(factor) {
        return new Duration(this.millis * factor);
    }
    addTo(time) {
        return time.plus(this);
    }
    toString() {
        const format = (n) => Math.floor(n).toString().padStart(2, '0');
        const seconds = Math.floor(this.millis / 1000);
        return [
            format(seconds / 60 / 60),
            format(seconds / 60 % 60),
            format(seconds % 60)
        ].join(':');
    }
    format(formatter) {
        return formatter(this.millis);
    }
}
Duration.ONE_SECOND = 1000;
